@use "../palette";
@use "../typography";

@use "gremlins/radio";
@use "gremlins/checkbox";
@use "gremlins/error";

@mixin gremlins {
  padding: 0;
  margin: 1rem 0;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  line-height: 1.375rem;

  &.radio {
    @include radio.gremlin-radio;
  }

  &.checkbox {
    @include checkbox.gremlin-checkbox;
  }

  &.error {
    @include error.gremlin-error;
  }

  &.fieldset {
    legend {
      @include typography.heading-m;

      // stylelint-disable-next-line no-duplicate-selectors
      & {
        padding: 0;
        margin-top: 0;
      }
    }

    &.error {
      @include error.gremlin-error;
    }
  }
}

%gremlins {
  @include gremlins;
}
